.str1 {stroke:#8d4604;stroke-width:105.83;stroke-miterlimit:22.9256}
.str0 {stroke:#373435;stroke-width:7.62;stroke-miterlimit:22.9256}
.fil0 {fill:#373435}
.fil1 {fill:#FDB415}
.fil2 {fill:#FDB415;fill-rule:nonzero}
.logoAimar{
    margin-top: 3px;
    transform: skew(22deg);
    width: 90px;
    height: auto;
    transition: all 1s ease;
}
.logoAimar.active{
    margin-top: 15px;
    width: 137px;
    height: auto;
    transition: all 1s ease;
}